import React from 'react'
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';

import ActionComp from './shared/actions'

const getPath = () => {
  let path = document.createElement("a");
  path.href = window.location.href;
  let url = path.pathname;
  path.remove();
  return url.replace('/page/','');
};

const getPageFromPath = (path, pages) => {
  let thePage;
  pages.map((page) => {
    if(page.post_name === path)
      thePage = page;
    return page;
  });
  return thePage;
};

const getPage = (props) => {
  let path = getPath();
  return getPageFromPath(path, props.data.pages);
};

const initializeClass = (props) => {
  let page = getPage(props);
  return {
    page:page,
    data:props.data
  };
};

const formatDate = (dateString) => {
  let date = new Date(dateString);
  let monthNames = ["January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
  ];
  return monthNames[date.getMonth()] + ' ' + date.getDate() + ', ' + (parseInt(date.getYear()) - 100 + 2000).toString();
};

const mockData = (String, num) => {
  let mock = Array.apply(null, Array(num)).map(() => {
    return String;
  });
  return mock.join(" ");
};

export default class extends React.Component {
  constructor(props){
    super(props);
    this.state = initializeClass(props);
  }
  componentWillReceiveProps(props){
    setTimeout(() => {
      this.setState(initializeClass(props));
    }, 200)
  }
  render(){
    return (
      <div>
        <Card>
          <CardMedia
            overlay={<CardTitle title={this.state.page.overlay_title} subtitle={this.state.page.overlay_description} />}
          >
            <img src={this.state.page.post_image} />
          </CardMedia>
          <CardTitle title={this.state.page.post_title} subtitle={formatDate(this.state.page.post_date)} />
          <CardText dangerouslySetInnerHTML={{__html:mockData(this.state.page.post_content, 99)}} />
          <CardActions>
            {
              this.state.page.actions.split(',').map((action) => {
                return (
                  <ActionComp action={action} />
                );
              })
            }
          </CardActions>
        </Card>
      </div>
    );
  }
}

